import React,{Fragment, useContext} from 'react'
import {Route,Redirect} from "react-router-dom"
import HomeView from './Views/HomeView'
import AdminView from './Views/AdminView'
import LoginView from './Views/LoginView'
import UbicanosView from './Views/UbicanosView'
import { EncabezadoContext } from "./context/EncabezadoContext";

export default function Routes(){
  const { userId } = useContext(EncabezadoContext);

  return(
    <Fragment>
      <Route path="/" exact component={HomeView} />
      <Route path="/admin" exact component ={AdminView}/>
      <Route path="/login" exact component ={LoginView}/>
      <Route path="/ubicanos" exact component ={UbicanosView}/>
      <Route
        path="/"
        exact
        render={() => {
          if(userId == false){
            return <Redirect to='/'/>
          }
        }}
      />
    </Fragment>
  )
}